<template>
    <div class="address_search"><!-- 장례식장 사이드메뉴-->
		<div class="address_search_inner">
			<h1>주소검색</h1>
			<div id="demo">
				<input type="radio" value="only" name="all" v-model="searchType" v-bind:checked="checked" > 장례식장 검색
				<input type="radio" value="all" name="all" v-model="searchType" v-bind:checked="unchecked" >모두 검색
			</div>
			<table>
			<colgroup>
				<col width="85%">
				<col width="15%">
			</colgroup>
			<tbody>
			<tr style="border-bottom:1px solid #cccccc;">
				<td><input type="text" placeholder="주소를 입력하세요" class="input01" v-model="address" @keyup.enter="searchAddress()"></td>
				<td style="text-align:right;"><button class="address_turn" @click="searchAddress"></button></td>
			</tr>
			</tbody>
			</table> 
			<div class="address" style="margin-top:10px;">
			<table cellspacing="0" cellpadding="0" border="0">
			<tr v-for="(address,index) in getAddressSearchList" :key="index" style="color:#999; border-bottom:1px solid #ccc;"  @click="registAddress(address)">
				<td width="90%" style="padding:8px 0 8px;">{{addressTxt(address)}}</td>
				<td style="text-align:right;"><button class="address_register" ></button></td>
			</tr>
			</table>  
		</div>
		</div>
		<button class="btn_address_colse" @click="close">닫기</button>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
	name:'AddressSearch',
	props:['method'],
	data(){
		return{
			address:'',
			searchType:'only'
		}
	},
	computed:{
		...mapGetters(['getAddressSearchList']),
	},
	methods:{
		addressTxt(address){
			
			if(address.road_address_name!==null && address.road_address_name!==undefined){
				return address.road_address_name +  ' ' + address.place_name
			}else{
				return address.funeralAddr2 + ' ' + address.funeralAddrName
			}
		},
		searchAddress(){
			if(this.searchType === 'only'){
				this.$store.dispatch('REQUEST_ADDRESS_SEARCH_FUNERAL_LIST',this.address)
			}else{
				this.$store.dispatch('REQUEST_ADDRESS_SEARCH_LIST',this.address)
			}
		},
		registAddress(address){
			
			let parameter ={}
			if(address.road_address_name!==null && address.road_address_name!==undefined){
				parameter ={
					address: address.road_address_name,
					place: address.place_name,
					phone: address.phone,
					x:address.x,
					y:address.y,
					funeralAddrCode:null
				}
				const context = this
				this.$store.dispatch('REQUEST_ADDRESS_SEARCH_POST_CODE',parameter).then(data => {
                  
					if (data.result) {
						parameter.post = data.post
					}

					
				
					context.method(parameter)
					context.$store.commit('END_DEEMED');
					context.close()
				})
				
				

			}else{
				parameter ={
				address: address.funeralAddr2,
				place: address.funeralAddrName,
				phone: address.funeralAddrPhone,
				x:address.funeralAddrX,
				y:address.funeralAddrY,
				memberFuneralAddr:{id:address.id}

				}
				this.method(parameter)
				this.$store.commit('END_DEEMED');
				this.close()
			}
		},
		close(){

            $('.address_search').removeClass( 'active' );
			this.$store.commit('END_DEEMED');
		}
	}
}
</script>