<template>
    <div style="width:100%; height:100%; display:block;">
        
        
        <div>
            <div class="deemedCustom"></div>
            <div id="login_wrapper" style="margin:0;overflow: scroll;">
                <div class="login_box topspace">
                    <div class="login_box_inner">
                        <h2>회원가입</h2>
                        <div><input type="text" class="login_input" v-model="member.id" placeholder="아이디입력"></div>
                        <div><input type="password" class="login_input" v-model="member.memberPw" placeholder="비밀번호입력"></div>
                        <div><input type="password" class="login_input" v-model="memberPwRe" placeholder="비밀번호 확인"></div>
                        <div class="address"><input type="text" class="login_input" v-model="member.memberName" placeholder="장례식장명"  readonly  @click="addrRegPopup"><span><a  @click="addrRegPopup">주소검색</a></span></div>
                        <div class="address"><input type="text" class="login_input" v-model="member.memberAddr" placeholder="장례식장 주소" readonly  @click="addrRegPopup"><span><a  @click="addrRegPopup">주소검색</a></span></div>
                        <div><input type="text" class="login_input" v-model="member.memberPhone" placeholder="전화번호(숫자만 입력)"  @keypress="onlyNumber" maxlength="11"></div>
                        <button class="btn_up"  ref="fileuploadbtn" @click="fileUpload"><div class="btn_up_txt">사업자등록증 업로드</div></button>
                        <input type="file" ref="fileupload" @change="changeFiles" multiple style="display:none;"/>
                        <div class="small_txt2">*사업자등록증을 업로드하셔야 가입이 완료됩니다.</div>
                        <button class="btn_login" @click="submitTerm">가입완료</button>		</div>
                </div>
                    <AddressSearch :method="registAddress"/>
                <TermPopup v-if="agreeFlag" :method="submit"/>
            </div>
        </div>
    </div>
</template>

<script>

import '@/assets/js/jquery-1.9.1.min.js';
import '@/assets/js/ux_common.js';
import  AddressSearch from '@/components/common/AddressSearch'
import  TermPopup from '@/components/common/TermPopup'
import myMixin from '@/mixin/mixin'
export default {
  name: 'JoinCorp',
  mixins: [myMixin],
  components: { 
      AddressSearch,
      TermPopup
	},
  	props: {
  	},
    data(){
        return{
            member: {
                id: "",
                memberAddr: "",
                memberPost: "",
                memberAddrX:'',
                memberAddrY:'',
                memberBirth:null,
                memberClass:"MEMBER",
                memberName:"",
                memberPhone:"",
                memberPush:null,
                memberPushYn:'Y',
                memberPw: "",
                memberType:"NORMAL",
                memberCorpMaster:'N',
                memberAttr:{
                    codeId:"TYPE-0" 
                },
                memberUseYN:{
                    codeId:"SUCCESS-1" 
                },//장례식장은 무조건 대기
                memberMarketingAgree:false
            },
            memberPwRe:null,
            files:[],
            agreeFlag:false
        }
    },
   	mounted() {
    },
    methods:{ 
        submitTerm(){
            var regType1 = /^[A-Za-z0-9+]*$/;
            if (!regType1.test(this.member.id)) { 
                alert('아이디는 숫자와 영어만 입력 가능합니다.'); 
                return;
            }
            if(!this.validataion()){
                return
            }
			this.$store.commit('START_DEEMED');
            this.agreeFlag =true
        },       
        submit(val){
            this.agreeFlag = false
			this.$store.commit('END_DEEMED');
            if(!this.validataion()){
                return
            }
            this.member.memberMarketingAgree= val
            const parameter={
                member: this.member,
                files: this.files
            }
	  	    this.$store.dispatch("REQUEST_JOIN", parameter) 
        },
        
        validataion(){
            //if(!this.emailCheck(this.member.id)){
            //    return false;
            //}
             if(!(this.member.id.length>=5 && this.member.id.length<=12)){
                alert('정확한 아이디를 입력해 주세요.');
                return false;
            }
            if(this.member.memberPw===null || this.member.memberPw===undefined || this.member.memberPw==='' ){
                alert('비밀번호를 입력해 주세요.');
                return false;
            }

            if(this.member.memberPw!==this.memberPwRe ){
                alert('비밀번호확인이 일치하지 않습니다.');
                return false;
            }

            
            if(this.member.memberName===null || this.member.memberName===undefined || this.member.memberName==='' ){
                alert('장례식장명을 입력해 주세요.');
                return false;
            }

            
            
            if(this.member.memberPhone===null || this.member.memberPhone===undefined || this.member.memberPhone==='' ){
                alert('전화번호를 입력해 주세요.');
                return false;
            }
            if(this.member.memberAddr===null || this.member.memberAddr===undefined || this.member.memberAddr==='' ){
                alert('장례식장 주소를 입력해 주세요.');
                return false;
            }
            if(this.files.length<1){
                alert('장례식장 인증파일을 업로드해 주세요.');
                return false;
            }

            return true;
            
        },
        changeFiles(event){
            this.files = event.target.files
        },
        fileUpload(){
            this.$refs.fileupload.click()
        },
        addrRegPopup(){
            $('.address_search').addClass( 'active' );
			this.$store.commit('START_DEEMED');
        },
        registAddress(val){
            this.member.memberName = val.place
            this.member.memberAddr = val.address
            this.member.memberPhone = val.phone.replace(/-/gi,"")
            this.member.memberAddrX = val.x
            this.member.memberAddrY = val.y
            this.member.memberPost = val.post
            this.member.memberFuneralAddr = val.memberFuneralAddr

        }
    }
}
</script>

